var bytecode = ""
var conadd = "0x991bCFFa4ED012D25C5071C43887F8D8d8Bdf8d4"

var cb;
var abiDefinition;
var contract;
var  estimatedGas;
var parameterValue;
var txnObject;
var web3;
var provider;

window.addEventListener("load", async () => {
  // Wait for loading completion to avoid race conditions with web3 injection timing.
  if (window.ethereum) {
    web3 = new Web3(window.ethereum);
    try {
      // Request account access if needed
      await window.ethereum.enable();
      $("#btnConnected").text("CONNECTED");
      // $("#btnConnected").style.color = "white";
      // $("#btnConnected").style.backgroundColor = "green !important";
      
      startApp();
    } catch (error) {
      console.error(error);
    }
  }
  // Legacy dapp browsers...
  else if (window.web3) {
    // Use MetaMask/Mist's provider.
    web3 = window.web3;
    $("#btnConnected").text("CONNECTED");
    $("#btnConnected").style.color = "white";
    $("#btnConnected").style.backgroundColor = "green !important";
    startApp();
    console.log("Injected web3 detected.");
  }
  // Fallback to localhost; use dev console port by default...
  else {
    provider = new Web3.providers.HttpProvider("http://127.0.0.1:9545");
    web3 = new Web3(provider);
    $("#constat").text("DISCONNECTED");
    document.getElementById("constat").style.color = "red";
    startApp();
    console.log("No web3 contract.methods injected, using Local web3.");
  }
});

